Grenbox* CreateGrenbox() {
	Grenbox* grenbox = malloc(sizeof(Grenbox));
	memset(grenbox, 0, sizeof(Grenbox));
	GLfloat tempx, tempz;
	do{
#ifdef __MINGW32__
		tempx = (double)rand() / (double)(RAND_MAX + 1) * (900 - 150)+ (double)150;
		tempz = (double)rand() / (double)(RAND_MAX + 1) * (900 - 150)+ (double)150;
#else
		tempx = (float)((rand()>>8)%1000);
		tempz = (float)((rand()>>8)%1000);
#endif
	}while(!isValidPoint(tempx,tempz));
	grenbox->dy = 0.0f;
	grenbox->x = tempx;
	grenbox->y = 100.0f;
	grenbox->z = tempz;
	grenbox->ry = tempx;
	grenbox->alive = 0.0f;
	return grenbox;
}
void TickGrenbox(Grenbox *p) {
	if(p->alive>0.5)
	{
		GLfloat getheight = getGroundHeight(p->x,p->z)+height;
		if(player->x < p->x+2.0 && player->x > p->x-2.0 &&	player->z < p->z+2.0 && player->z > p->z-2.0)
		{
			grenades++;
			p->alive = 0.0f;
		}
		if(p->y<getheight)
		{
			
		}

		else
		{
			GLfloat change = gravityTick(&p->dy);
			p->y += change;
		}
	
	//	drawGrenbox(p->x,p->y,p->z, p->ry, 0);
	}

	
}	
