#include "BI_player.h"
#include "../BI_physics.h"
#include "../BunnyIsland.h"
#include "../BI_draw.h"
#include <string.h>
#include <stdio.h>
#include <stdlib.h>

#define height 2.0

Player* CreatePlayer(void) {
	Player* player = malloc(sizeof(Player));
	memset(player, 0, sizeof(Player));
	player->x = 0;
	player->y = 20;
	player->z = 0;
	player->ry = 0;
	player->dx = 0;
	player->dy = 0;
	player->dz = 0;
	player->hp = 0;
	return player;
}
void TickPlayer(Player *p) {
	p->x = lookAtMatrix[0];
	p->z = lookAtMatrix[2];
	//printf("height: %f\n", p->y);
	GLfloat getheight = getGroundHeight(p->x,p->z)+height;
	if(lookAtMatrix[1]>getheight || p->dy>0.00001)
	{
		GLfloat change = gravityTick(&p->dy);
		lookAtMatrix[1]+= change;
		lookAtMatrix[4]+= change;
	}
	else
	{		
		p->dy=0;
		GLfloat diff = getheight - lookAtMatrix[1];
		lookAtMatrix[1]+=diff;
		lookAtMatrix[4]+=diff;
	}
	p->y = lookAtMatrix[1];

}	
