#include "BI_orc.h"
#include "BI_arrow.h"
#include "math.h"
#include "../BI_physics.h"
#include "../BunnyIsland.h"
#include "../BI_draw.h"
#include <string.h>
#include <stdio.h>
#include <stdlib.h>

#define OrcSizeX 1.0
#define OrcSizeY 2.0
#define OrcSizeZ 1.0



Orc* CreateOrc(void) {
	Orc* orc = malloc(sizeof(Orc));
	memset(orc, 0, sizeof(Orc));
	orc->x = OrcX;
	orc->y = 60;
	orc->z = OrcZ;
	orc->ry = 0;
	orc->ds = 0.1f; //speed
	orc->dy = 0;
	orc->hp = 100;
	orc->size = 1.0f;
	return orc;
}
void TickOrc(Orc *b) {

	GLfloat getheight = getGroundHeight(b->x,b->z);

	if(b->hp > 0) {
		if(arrow->alive>0.5)
		if(arrow->x < b->x+OrcSizeX*b->size && arrow->x > b->x-OrcSizeX*b->size && arrow->y < b->y+OrcSizeY*b->size && arrow->y > b->y-OrcSizeY*b->size && arrow->z < b->z+OrcSizeZ*b->size && arrow->z > b->z-OrcSizeZ*b->size)
		{
			b->hp--;
			b->ry+=3.14;
			arrow->alive=0.0f;
		}
		if(b->y > getheight || b->dy > 0.0001)
		{
			GLfloat change = gravityTick(&b->dy);
			//printf("Orc cos height: %f\n", cos(b->ry));
			b->y += change;
			GLfloat tempx = b->x + sin(b->ry)*b->ds*ticktid/17;
			GLfloat tempz = b->z - cos(b->ry)*b->ds*ticktid/17;
			if(isValidPoint(tempx,tempz)) {
				b->x = tempx;
				b->z = tempz;
				
			}
		}
		else
		{		
			GLfloat tempx = b->x + sin(b->ry)*b->ds;
			GLfloat tempz = b->z - cos(b->ry)*b->ds;
			if(!isValidPoint(tempx,tempz)) {
				b->ry+=3.14;
				
			}
				b->ry += (float)(((rand()>>8)%(2*314))/1000.0f);
				b->ry -= (float)(((rand()>>8)%(2*314))/1000.0f);
				
				
			b->dy = (float)(5.0f+(rand()>>8)%3)+b->size;
			if(b->dy>40) b->dy = 40.0f;
			GLfloat diff = getheight - b->y;
			b->y+=diff;

		}
	}
	else {
		if(b->y > getheight || b->dy > 0.0001)
		{
			GLfloat change = gravityTick(&b->dy);
			//printf("Orc y height: %f %f\n", b->y, getheight);
			b->y += change;
		}
		else if(b->y != getheight) {
			b->y = getheight;
		}
	}
	//printf("height2: %f\n", getheight);
	//printf("height2: %f\n", b->y);
	//drawOrc(b->x, b->y, b->z, b->ry, b->size, 0);
}	
