#include "BI_arrow.h"
#include "../BI_physics.h"
#include "../BunnyIsland.h"
#include "../BI_draw.h"
#include <string.h>
#include <stdio.h>
#include <stdlib.h>
#include <math.h>

#define height 0.2
#define speed 4.0

Arrow* CreateArrow(GLfloat x,GLfloat y,GLfloat z,GLfloat dx,GLfloat dy,GLfloat dz, GLfloat ry) {
	Arrow* arrow = malloc(sizeof(Arrow));
	memset(arrow, 0, sizeof(Arrow));
	arrow->x = x;
	arrow->y = y;
	arrow->z = z;
	arrow->ry = ry;
	arrow->dx = dx;
	arrow->dy = dy;
	arrow->dz = dz;
	arrow->alive = 1.0f;
	return arrow;
}
void TickArrow(Arrow *p) {
	GLfloat getheight = getGroundHeight(p->x,p->z)+height;
		
	if(p->y<getheight || arrow->alive<0.5)
	{
		p->dx=0;
		p->dy=0;
		p->dz=0;
		
	}

	else
	{
		GLfloat changex, changey, changez; //vector between old coords and new coords
		changex = p->dx*speed*ticktid/17;
		changey = gravityTick(&p->dy);
		changez = p->dz*speed*ticktid/17;
		
		p->rz = asin(-changey/(sqrt(changex*changex+changey*changey+changez*changez)));
		
		p->x += changex;
		p->y += changey;
		p->z += changez;

	}
		
}	
