#include "BI_bunny.h"
#include "BI_arrow.h"
#include "math.h"
#include "../BI_physics.h"
#include "../BunnyIsland.h"
#include "../BI_draw.h"
#include <string.h>
#include <stdio.h>
#include <stdlib.h>


Bunny* CreateBunny(void) {
	Bunny* bunny = malloc(sizeof(Bunny));
	memset(bunny, 0, sizeof(Bunny));
	bunny->x = 728.0f;
	bunny->y = 60;
	bunny->z = 253.0f;
	bunny->ry = 0;
	bunny->ds = 0.1f; //speed
	bunny->dy = 0;
	bunny->hp = 1;
	return bunny;
}
void TickBunny(Bunny *b) {

	GLfloat getheight = getGroundHeight(b->x,b->z);

	if(b->hp > 0) {
		if(arrow->alive>0.5)
		if(arrow->x < b->x+1.0 && arrow->x > b->x-1.0 && arrow->y < b->y+2.0 && arrow->y > b->y-1.0 &&	arrow->z < b->z+1.0 && arrow->z > b->z-1.0)
		{
			b->hp--;
			arrow->alive = 0.0f;
		}
		if(b->y > getheight || b->dy > 0.0001)
		{
			GLfloat change = gravityTick(&b->dy);
			//printf("bunny cos height: %f\n", cos(b->ry));
			b->y += change;
			GLfloat tempx = b->x + sin(b->ry)*b->ds*ticktid/17;
			GLfloat tempz = b->z - cos(b->ry)*b->ds*ticktid/17;
			if(isValidPoint(tempx,tempz)) {
				b->x = tempx;
				b->z = tempz;
				//inside castle;
				if((b->x > (CastleX-CastleHitBox)) && (b->x < (CastleX+CastleHitBox))
						 && (b->z > (CastleZ-CastleHitBox)) && (b->z < (CastleZ+CastleHitBox))) {
					castlehp--;
					b->x = orc->x;	//should be orc position
					b->y = orc->y;
					b->z = orc->z;
					
					
				}
			}
		}
		else
		{		
			//if(((rand()>>8)%2)) {
			GLfloat distance = 10000.0f;
			GLfloat tempdistance = 0.0f;
			GLfloat tempx;
			GLfloat tempz;
			GLfloat tempry;
			for(int i=0;i<5;i++) {
#ifdef __MINGW32__
				tempry = (rand() / (double)(RAND_MAX + 1) * (double)(2*3.14));
#else
				tempry = (float)(((rand()>>8)%(2*314))/100.0f);
#endif
				tempx = b->x + sin(tempry)*b->ds;
				tempz = b->z - cos(tempry)*b->ds;
				if(isValidPoint(tempx,tempz)) {
					tempdistance = sqrt((CastleX - tempx)*(CastleX - tempx) + (CastleZ - tempz)*(CastleZ - tempz));
					if(tempdistance < distance) {
						distance = tempdistance;

						b->ry = tempry;
					}
				}
				
			}
				//printf("bunny ry: %f\n", b->ry);
			b->dy = (float)(5.0f+(rand()>>8)%3);
			GLfloat diff = getheight - b->y;
			b->y+=diff;

		}
	}
	else {
		if(b->y > getheight || b->dy > 0.0001)
		{
			GLfloat change = gravityTick(&b->dy);
			//printf("bunny y height: %f %f\n", b->y, getheight);
			b->y += change;
		}
		else if(b->y != getheight) {
			b->y = getheight;
		}
	}
	//printf("height2: %f\n", getheight);
	//printf("height2: %f\n", b->y);
	//drawBunny(b->x, b->y+1.2f, b->z, b->ry, 0);
}	
