#include "BI_terrain.h"
#include "VectorUtils2.h"

int terrainWidth, terrainHeight;
#define scale 0.5
#define heightScale 15.0

int isValidPointTerrain(GLfloat x, GLfloat z) {
	return (scale*x<1 || scale*x>terrainWidth-1 || scale*z<1 || scale*z>(terrainHeight-1)) ? 0:1;
}


GLfloat getHeightValue(GLfloat x, GLfloat z, Model* tm)
{
	GLfloat y = 0.0;
	Point3D normal;
	// Triangle 1
	if(scale*x<1 || scale*x>terrainWidth-1 || scale*z<1 || scale*z>(terrainHeight-1)) return 0.0;
	int x_scaled = (int)x * scale;
	int z_scaled = (int)z * scale;
	int i = ( x_scaled + z_scaled * (terrainWidth-1))*6; // index of first vertex of triangle 1 in current quad in index array
	int vertex_x = tm->vertexArray[(tm->indexArray[i])*3];	
	int vertex_y = tm->vertexArray[(tm->indexArray[i])*3+1];	
	int vertex_z = tm->vertexArray[(tm->indexArray[i])*3+2];
	if((x-vertex_x) + (z-vertex_z) <= 1)
{// triangel 1
		
			//vektor 1 fr triangle 1:
			Point3D p1;
			//printf("%d r %d",(tm->indexArray[i])*3,3*(vertex_x+vertex_z*terrainWidth));
			p1.x = tm->vertexArray[(tm->indexArray[i+1])*3 +0]- tm->vertexArray[(tm->indexArray[i])*3 +0];
			p1.y = tm->vertexArray[(tm->indexArray[i+1])*3 +1] - tm->vertexArray[(tm->indexArray[i])*3 +1];
			p1.z = tm->vertexArray[(tm->indexArray[i+1])*3 +2] - tm->vertexArray[(tm->indexArray[i])*3 +2];

			//vektor 2 fr triangle 1:
			Point3D p2;
			p2.x = tm->vertexArray[(tm->indexArray[i+2])*3 +0] - tm->vertexArray[(tm->indexArray[i])*3 +0];
			p2.y = tm->vertexArray[(tm->indexArray[i+2])*3 +1] - tm->vertexArray[(tm->indexArray[i])*3 +1];
			p2.z = tm->vertexArray[(tm->indexArray[i+2])*3 +2] - tm->vertexArray[(tm->indexArray[i])*3 +2];


			//Normal for triangle 1:

			CrossProduct(&p2,&p1,&normal);
			Normalize(&normal);


	}

	else if((x-vertex_x) + (z-vertex_z) > 1)
{	// triangel 2 
		//	printf("TRIANGEL 2\n");
			//vektor 1 fr triangle 1:
			Point3D p1;
			//printf("%d r %d",(tm->indexArray[i])*3,3*(vertex_x+vertex_z*terrainWidth));
			p1.x = tm->vertexArray[(tm->indexArray[i+3])*3 +0]- tm->vertexArray[(tm->indexArray[i+5])*3 +0];
			p1.y = tm->vertexArray[(tm->indexArray[i+3])*3 +1] - tm->vertexArray[(tm->indexArray[i+5])*3 +1];
			p1.z = tm->vertexArray[(tm->indexArray[i+3])*3 +2] - tm->vertexArray[(tm->indexArray[i+5])*3 +2];

			//vektor 2 fr triangle 1:
			Point3D p2;
			p2.x = tm->vertexArray[(tm->indexArray[i+4])*3 +0] - tm->vertexArray[(tm->indexArray[i+5])*3 +0];
			p2.y = tm->vertexArray[(tm->indexArray[i+4])*3 +1] - tm->vertexArray[(tm->indexArray[i+5])*3 +1];
			p2.z = tm->vertexArray[(tm->indexArray[i+4])*3 +2] - tm->vertexArray[(tm->indexArray[i+5])*3 +2];


			//Normal for triangle 1:

			CrossProduct(&p2,&p1,&normal);
			Normalize(&normal);

}



else return -1; // UNDEFINED

			GLfloat d_plane;
			d_plane = normal.x*vertex_x + normal.y*vertex_y + normal.z*vertex_z;
			y = -(normal.x*x + normal.z*z - d_plane)/normal.y;



	return y;
}


Model* GenerateTerrain(TextureData *tex,
 GLuint program,
 char* vertexVariableName,
 char* normalVariableName,
 char* texCoordVariableName)
{
	int vertexCount = tex->width * tex->height;
	int triangleCount = (tex->width-1) * (tex->height-1) * 2;
	int x, z;

	terrainWidth = tex->width;
	terrainHeight = tex->height;
	
	Model* model = malloc(sizeof(Model));
	memset(model, 0, sizeof(Model));
	
	model->vertexArray = malloc(sizeof(Model));
	
	model->vertexArray = malloc(sizeof(GLfloat) * 3 * vertexCount);
	model->normalArray = malloc(sizeof(GLfloat) * 3 * vertexCount);
	model->texCoordArray = malloc(sizeof(GLfloat) * 2 * vertexCount);
	model->indexArray = malloc(sizeof(GLuint) * triangleCount*3);
	
	model->numVertices = vertexCount;
	model->numIndices = triangleCount*3;
	
	for (x = 0; x < tex->width; x++)
		for (z = 0; z < tex->height; z++)
		{
// Vertex array. You need to scale this properly
			model->vertexArray[(x + z * tex->width)*3 + 0] = x / scale;
			model->vertexArray[(x + z * tex->width)*3 + 1] = tex->imageData[(x + z * tex->width) * (tex->bpp/8)] / heightScale;
		//	printf("y: %f \n",tex->imageData[(x + z * tex->width) * (tex->bpp/8)] / heightScale);
			model->vertexArray[(x + z * tex->width)*3 + 2] = z / scale;
// Normal vectors. You need to calculate these.
			model->normalArray[(x + z * tex->width)*3 + 0] = 0.0;
			model->normalArray[(x + z * tex->width)*3 + 1] = 0.0;
			model->normalArray[(x + z * tex->width)*3 + 2] = 0.0;
// Texture coordinates. You may want to scale them.
			model->texCoordArray[(x + z * tex->width)*2 + 0] = x/10.0; // (float)x / tex->width;
			model->texCoordArray[(x + z * tex->width)*2 + 1] = z/10.0; // (float)z / tex->height;
		}
	for (x = 0; x < tex->width-1; x++)
		for (z = 0; z < tex->height-1; z++)
		{

			
		// Triangle 1
			model->indexArray[(x + z * (tex->width-1))*6 + 0] = x + z * tex->width;
			model->indexArray[(x + z * (tex->width-1))*6 + 1] = x + (z+1) * tex->width;
			model->indexArray[(x + z * (tex->width-1))*6 + 2] = x+1 + z * tex->width;

		// Triangle 2
			model->indexArray[(x + z * (tex->width-1))*6 + 3] = x+1 + z * tex->width;
			model->indexArray[(x + z * (tex->width-1))*6 + 4] = x + (z+1) * tex->width;
			model->indexArray[(x + z * (tex->width-1))*6 + 5] = x+1 + (z+1) * tex->width;

			//vektor 1 fr triangle 1:
			Point3D p1;
			p1.x = model->vertexArray[3*(x+1+z*tex->width) +0]- model->vertexArray[3*(x+z*tex->width) +0];
			p1.y = model->vertexArray[3*(x+1+z*tex->width) +1] - model->vertexArray[3*(x+z*tex->width) +1];
			p1.z = model->vertexArray[3*(x+1+z*tex->width) +2] - model->vertexArray[3*(x+z*tex->width) +2];

			//vektor 2 fr triangle 1:
			Point3D p2;
			p2.x = model->vertexArray[3*(x+(z+1)*tex->width)] - model->vertexArray[3*(x+z*tex->width) +0];
			p2.y = model->vertexArray[3*(x+(z+1)*tex->width) +1] - model->vertexArray[3*(x+z*tex->width) +1];
			p2.z = model->vertexArray[3*(x+(z+1)*tex->width) +2] - model->vertexArray[3*(x+z*tex->width) +2];


			//Normal for triangle 1:
			Point3D normal;
			CrossProduct(&p2,&p1,&normal);
			Normalize(&normal);

			//printf("vektor p1: (%f,%f,%f)", p1.x,p1.y,p1.z);



			//Triangle 1 vertex 1
			model->normalArray[3*(x + z * tex->width) + 0] += normal.x;
			model->normalArray[3*(x + z * tex->width) + 1] += normal.y;
			model->normalArray[3*(x + z * tex->width) + 2] += normal.z;
			//Triangle 1 vertex 2
			model->normalArray[3*(x + (z+1) * tex->width) + 0] += normal.x;
			model->normalArray[3*(x + (z+1) * tex->width) + 1] += normal.y;
			model->normalArray[3*(x + (z+1) * tex->width) + 2] += normal.z;
			//Triangle 1 vertex 3
			model->normalArray[3*(x+1 + z * tex->width) + 0] += normal.x;
			model->normalArray[3*(x+1 + z * tex->width) + 1] += normal.y;
			model->normalArray[3*(x+1 + z * tex->width) + 2] += normal.z;


			//vektor 1 fr triangle 2:
			p1.x = model->vertexArray[3*(x+1 + z * tex->width)] - model->vertexArray[3*(x+1 + (z+1) * tex->width)];
			p1.y = model->vertexArray[3*(x+1 + z * tex->width) +1] - model->vertexArray[3*(x+1 + (z+1) * tex->width) +1];
			p1.z = model->vertexArray[3*(x+1 + z * tex->width) +2] - model->vertexArray[3*(x+1 + (z+1) * tex->width) +2];

			//vektor 2 fr triangle 2
			p2.x = model->vertexArray[3*(x+(z+1)*tex->width)] - model->vertexArray[3*(x+1 + (z+1) * tex->width)];
			p2.y = model->vertexArray[3*(x+(z+1)*tex->width) +1] - model->vertexArray[3*(x+1 + (z+1) * tex->width) +1];
			p2.z = model->vertexArray[3*(x+(z+1)*tex->width) +2] - model->vertexArray[3*(x+1 + (z+1) * tex->width) +2];

			//Normal for triangle 2:
			CrossProduct(&p1,&p2,&normal);
			Normalize(&normal);
			//printf("%f \n",normal.z);
			
			//Triangle 2 vertex 1
			model->normalArray[3*(x+1 + z * tex->width) + 0] += normal.x;
			model->normalArray[3*(x+1 + z * tex->width) + 1] += normal.y;
			model->normalArray[3*(x+1 + z * tex->width) + 2] += normal.z;
			//Triangle 2 vertex 2
			model->normalArray[3*(x + (z+1) * tex->width) + 0] += normal.x;
			model->normalArray[3*(x + (z+1) * tex->width) + 1] += normal.y;
			model->normalArray[3*(x + (z+1) * tex->width) + 2] += normal.z;
			//Triangle 2 vertex 3
			model->normalArray[3*(x+1 + (z+1) * tex->width) + 0] += normal.x;
			model->normalArray[3*(x+1 + (z+1) * tex->width) + 1] += normal.y;
			model->normalArray[3*(x+1 + (z+1) * tex->width) + 2] += normal.z;

		}
	
	for(int i=0;i<3 * vertexCount;i=i+3)
	{
		//printf("%f",model->normalArray[i]);
		Point3D normal;
		normal.x = model->normalArray[i];
		normal.y = model->normalArray[i+1];
		normal.z = model->normalArray[i+2];	
		Normalize(&normal);
		model->normalArray[i] = normal.x;
		model->normalArray[i+1] = normal.y;
		model->normalArray[i+2] = normal.z;

		//printf("normal: (%f,%f,%f)\n",normal.x,normal.y,normal.z);
	}


	// End of terrain generation
	
	// Upload and set variables like LoadModelPlus
	//glGenVertexArrays(1, &model->vao);
	glGenBuffers(1, &model->vb);
	glGenBuffers(1, &model->ib);
	glGenBuffers(1, &model->nb);
	if (model->texCoordArray != NULL)
		glGenBuffers(1, &model->tb);
	
	//glBindVertexArray(model->vao);

	// VBO for vertex data
	glBindBuffer(GL_ARRAY_BUFFER, model->vb);
	glBufferData(GL_ARRAY_BUFFER, model->numVertices*3*sizeof(GLfloat), model->vertexArray, GL_STATIC_DRAW);
	glVertexAttribPointer(glGetAttribLocation(program, vertexVariableName), 3, GL_FLOAT, GL_FALSE, 0, 0); 
	glEnableVertexAttribArray(glGetAttribLocation(program, vertexVariableName));

	// VBO for normal data
	glBindBuffer(GL_ARRAY_BUFFER, model->nb);
	glBufferData(GL_ARRAY_BUFFER, model->numVertices*3*sizeof(GLfloat), model->normalArray, GL_STATIC_DRAW);
	glVertexAttribPointer(glGetAttribLocation(program, normalVariableName), 3, GL_FLOAT, GL_FALSE, 0, 0);
	glEnableVertexAttribArray(glGetAttribLocation(program, normalVariableName));

	// VBO for texture coordinate data
	if (model->texCoordArray != NULL)
	{
		glBindBuffer(GL_ARRAY_BUFFER, model->tb);
		glBufferData(GL_ARRAY_BUFFER, model->numVertices*2*sizeof(GLfloat), model->texCoordArray, GL_STATIC_DRAW);
		glVertexAttribPointer(glGetAttribLocation(program, texCoordVariableName), 2, GL_FLOAT, GL_FALSE, 0, 0);
		glEnableVertexAttribArray(glGetAttribLocation(program, texCoordVariableName));
	}
	
	glBindBuffer(GL_ELEMENT_ARRAY_BUFFER, model->ib);
	glBufferData(GL_ELEMENT_ARRAY_BUFFER, model->numIndices*sizeof(GLuint), model->indexArray, GL_STATIC_DRAW);
	
	model->program = program;
	return model;
}
