GLuint program, tex_grass, tex_dirt, tex_sky, tex_orc, tex_water, tex_sand, tex_arrow, tex_bunny, tex_grenbox;
TextureData height_map,water_map;
Model *wind_balc, *ground, *wind_roof, *wind_wall, *wind_blade, *castle_wall, *castle_roof, *tree, *bunnymodel, *skybox, *arrowmodel, *water, *lightpostmodel, *orcmodel, *grenboxmodel;

