	x = 284.0; z = 488.0;
	lpArray[1] = CreateLightpost(x,getGroundHeight(x,z),z);
	x = 370.0; z = 360.0;
	lpArray[2] = CreateLightpost(x,getGroundHeight(x,z),z);
	x = 493.0; z = 279.0;
	lpArray[3] = CreateLightpost(x,getGroundHeight(x,z),z);
	x = 638.0; z = 259.0;
	lpArray[4] = CreateLightpost(x,getGroundHeight(x,z),z);
	x = 663.0; z = 452.0;
	lpArray[5] = CreateLightpost(x,getGroundHeight(x,z),z);
