void drawSky(void) {
	glDisable(GL_DEPTH_TEST);
	glDisable(GL_CULL_FACE);
	glActiveTexture(GL_TEXTURE1);
	glBindTexture(GL_TEXTURE_2D, tex_sky);
	glActiveTexture(GL_TEXTURE0);
	glBindTexture(GL_TEXTURE_2D, tex_sky);
	IdentityMatrix(transformationMatrix);
	temp_x = cameraMatrix[3];
	temp_y = cameraMatrix[7];
	temp_z = cameraMatrix[11];
	cameraMatrix[3] = 0.0	;
	cameraMatrix[7] = 0.0;
	cameraMatrix[11] = 0.0;
	uploadMatrices(0);
	DrawModel(skybox);
	glEnable(GL_DEPTH_TEST);
	glCullFace(GL_BACK);
	glEnable(GL_CULL_FACE);
	cameraMatrix[3] = temp_x;
	cameraMatrix[7] = temp_y;
	cameraMatrix[11] = temp_z;	
	uploadMatrices(0);
}

void drawWater(void) {
	glDisable(GL_CULL_FACE);
	T(0.0, -6.033333, 0.0, translationMatrix);
	IdentityMatrix(rotationMatrix);
	Mult(translationMatrix,rotationMatrix,transformationMatrix);
	glActiveTexture(GL_TEXTURE1);
	glBindTexture(GL_TEXTURE_2D, tex_water);
	glActiveTexture(GL_TEXTURE0);
	glBindTexture(GL_TEXTURE_2D, tex_water);
	isWater[0] = 1;
	glUniform1iv(glGetUniformLocation(program, "isWater"), 1, isWater);
	uploadMatrices(0);
	DrawModel(water);
	isWater[0] = 0;
	glUniform1iv(glGetUniformLocation(program, "isWater"), 1, isWater);
	glCullFace(GL_BACK);
	glEnable(GL_CULL_FACE);

}

void drawGround(int reflect) {
	IdentityMatrix(transformationMatrix);
	glActiveTexture(GL_TEXTURE1);
	glBindTexture(GL_TEXTURE_2D, tex_sand);
	glActiveTexture(GL_TEXTURE0);
	glBindTexture(GL_TEXTURE_2D, tex_grass);
	uploadMatrices(reflect);
	DrawModel(ground);
}




void drawText(GLfloat posX, GLfloat posY, char* string) {
	glDisable(GL_DEPTH_TEST);
	isText[0] = 1;
	glUniform1iv(glGetUniformLocation(program, "isText"), 1, isText);	
	glUniformMatrix4fv(glGetUniformLocation(program, "cameraMatrix"), 1, GL_FALSE, tempMatrix);
	glUniformMatrix4fv(glGetUniformLocation(program, "mvpMatrix"), 1, GL_FALSE, tempMatrix);
	glUniformMatrix4fv(glGetUniformLocation(program, "mvMatrix"), 1, GL_FALSE, tempMatrix);
	glUniformMatrix4fv(glGetUniformLocation(program, "mMatrix"), 1, GL_FALSE, tempMatrix);
	glUniformMatrix3fv(glGetUniformLocation(program, "normalMatrix"), 1, GL_FALSE, tempMatrix);
	//glRasterPos2f(posX, posY); need workaround
	for(int i=0;i<strlen(string); i++)
	{
		glutBitmapCharacter(GLUT_BITMAP_TIMES_ROMAN_24, string[i]);
	}
	isText[0] = 0;
	glUniform1iv(glGetUniformLocation(program, "isText"), 1, isText);
	glEnable(GL_DEPTH_TEST);
}

void drawLightpost(GLfloat x, GLfloat z, int reflect) {
	glActiveTexture(GL_TEXTURE1);
	glBindTexture(GL_TEXTURE_2D, tex_sand);
	glActiveTexture(GL_TEXTURE0);
	glBindTexture(GL_TEXTURE_2D, tex_sand);
	T(x, getHeightValue(x,z, ground), z, translationMatrix);
	translationMatrix[0] = 0.007 ; translationMatrix[5] = 0.007; translationMatrix[10] = 0.007;

	IdentityMatrix(rotationMatrix);
	Mult(translationMatrix,rotationMatrix,transformationMatrix);
	uploadMatrices(reflect);
	DrawModel(lightpostmodel);
}

void drawArrow(GLfloat x, GLfloat y, GLfloat z, GLfloat ry, GLfloat rz, int reflect) {
	glActiveTexture(GL_TEXTURE1);
	glBindTexture(GL_TEXTURE_2D, tex_sand);
	glActiveTexture(GL_TEXTURE0);
	glBindTexture(GL_TEXTURE_2D, tex_sand);
	T(x, y, z, translationMatrix);
	translationMatrix[0] = 10.1 ; translationMatrix[5] = 10.1 ; translationMatrix[10] = 10.1 ;
	Ry(PI/2+ry,rotationMatrix);
	Rz(rz,rotationMatrix2);
	Mult(rotationMatrix, rotationMatrix2, rotationMatrix);
	
	//arrow points in -x by default
	//ry is rotation around y axis, 0 is z
	Mult(translationMatrix,rotationMatrix,transformationMatrix);
	uploadMatrices(reflect);
	DrawModel(arrowmodel);
}

int isValidPoint(GLfloat x, GLfloat z) {
	// if(getHeightValue(x,z,ground) < 3.0) return false; // removed to simplify bunny AI
	if(x<200 || x>830 || z<190 || z >850) return false; // if in the ocean
	return true;

}

