Lightpost* CreateLightpost(GLfloat x,GLfloat y,GLfloat z) {
	Lightpost* lp = malloc(sizeof(Lightpost));
	memset(lp, 0, sizeof(Lightpost));
	lp->x = x;
	lp->y = y;
	lp->z = z;
	return lp;
}
